.post-type-archive-glampings .site {
    background: var(--chalk);
}

.archive-glampings {
    display: flex;
    gap: 16px;
    padding-bottom: 40px;

    &__left {
        width: 860px;
        flex: none;
        display: flex;
        gap: 16px;

        &.no-map {
            width: 100%;
        }
    }

    & .rating-count {
        font-size: 12px;
    }
}

.glampings-filtr {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 240px;
    flex: none;
}

.glampings-items {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
    width: calc(100% - 288px);
    padding-right: 10px;

    @media (max-width: 1199px) {
        width: calc(100% - 272px);
    }

    @media (max-width: 767px) {
        width: calc(100% - 256px);
    }

    @media (max-width: 499px) {
        width: 100%;
    }

    &.no-sidebar {
        width: 100%;
    }

    &.list {
        flex-direction: column;
        overflow: auto;
        flex-wrap: nowrap;
        height: 82vh;

        & .glamping-item {

            &__thumbnail {
                position: relative;
                width: 210px;
                height: 200px;
            }
        }
    }

    &.card {
        height: fit-content;
        // flex-direction: row;

        & .glamping-item {
            flex-direction: column;
            width: calc((100% - 48px) / 4);
            background: #FFFFFF;

            @media (max-width: 1199px) {
                width: calc((100% - 32px) / 3);
            }

            @media (max-width: 767px) {
                width: calc((100% - 16px) / 2);
            }

            @media (max-width: 499px) {
                width: 100%;
            }

            &__thumbnail {
                position: relative;
                width: 100%;
                height: 200px;
            }

            &__content {
                flex-direction: column;
                // flex: none;

                &__left {
                    flex: none;
                }

                &__bottom {
                    margin-top: 10px;
                }

                &__right {
                    flex-direction: row;
                    align-items: flex-end;
                    justify-content: space-between;
                    width: 100%;
                    margin-top: auto;

                    &__btn {

                        &.btn-delete {
                            z-index: 4;
                        }
                    }
                }
            }
        }
    }
}

.glampings-map {
    display: none;
    max-width: calc(100% - 860px);
    width: 100%;

    &.active {
        display: block;
    }

    &__content {
        width: 100%;
        height: 100%;
    }
}

.glamping-item {
    position: relative;
    flex: none;
    display: flex;
    border-radius: $border-radius-single;
    background: #FFFFFF;
    overflow: hidden;

    &:hover {
        // & img {
        //     transform: scale($scale);
        // }

        & button.btnvib {
            border: 1px solid var(--Primary-700, #3033D1);
            background: var(--Primary-700, #3033D1);
        }

        & .glamping-item__content__title {
            color: var(--Primary-700, #3033D1);
        }
    }

    &__url {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
    }

    &__recommended {
        position: absolute;
        top: 10px;
        left: 10px;
        font-size: 14px;
        color: #fff;
        background: var(--color-bg-recomended);
        padding: 2px 6px;
        border-radius: 8px;
        z-index: 9;
    }

    &__btns-fav-comp {
        position: absolute;
        top: 5px;
        right: 5px;
        z-index: 9;

        &__btn-add-fav {
            position: absolute;
            top: 5px;
            right: 5px;
            z-index: 9;
            width: 30px;
            height: 30px;
            padding: 8px;
        }

        &__btn-add-comp {
            position: absolute;
            top: 40px;
            right: 5px;
            z-index: 9;
            width: 30px;
            height: 30px;
            padding: 8px;

            &.page-favorites {
                top: 5px;
            }
        }
    }

    &__thumbnail {
        overflow: hidden;
        z-index: 4;

        & img {
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.4s;

            &:hover {
                transform: scale($scale);
            }
        }
    }

    &__content {
        flex: 1;
        display: flex;
        gap: 12px;
        padding: 16px;

        &__left {
            flex: 1;
            display: flex;
            flex-direction: column;
        }

        &__right {
            flex: none;
            display: flex;
            flex-direction: column;
            gap: 6px;
            width: 124px;

            &__price {
                display: flex;
                flex-direction: column;
                // margin-bottom: 6px;
                height: 100%;

                & span {
                    display: block;
                    font-size: 12px;

                    &.price-number {
                        font-size: 22px;
                        font-weight: 700;
                    }

                    &.price-text {
                        margin-top: auto;
                        color: var(--text-color-light);
                    }
                }
            }

            &__btn {

                & span {
                    display: block;
                    font-size: 12px;
                    color: var(--text-color-light);
                }
            }
        }

        &__title {
            font-weight: 600;
        }

        &__rating {
            display: flex;
            gap: 8px;
            flex-wrap: wrap;
        }

        &__bottom {
            margin-top: auto;
            font-size: 12px;
            color: var(--text-color-light);

            &__type {
                display: flex;
                align-items: center;
                gap: 4px;
            }

            &__address {

                & a {

                    & svg {

                        path {
                            fill: var(--text-color-light);
                        }
                    }
                }
            }
        }
    }
}

.archive-glampings {

    & .glampings-items {

        &.card {

            & .glamping-item {
                width: calc((100% - 48px) / 4);
                // width: calc((100% - 64px) / 5);

                // @media (max-width: 1649px) {
                //     width: calc((100% - 48px) / 4);
                // }

                @media (max-width: 1439px) {
                    width: calc((100% - 32px) / 3);
                }

                @media (max-width: 1099px) {
                    width: calc((100% - 16px) / 2);
                }

                @media (max-width: 499px) {
                    width: 100%;
                }
            }
        }
    }
}

.swiper-pagination-bullet-active {
    background: #FFFFFF;
}

.swiper-button-next, .swiper-button-prev {
    color: #FFFFFF;

    &:after {
        font-size: 22px;
    }
}

.glc-icon-content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 24px;
    background: #FFFFFF; //var(--color-svg-form);
    border-radius: 12px;
    border: 1px solid var(--color-primary);
    font-weight: 600;
    color: var(--color-primary);

    &:before {
    	content: "";
    	border: solid transparent;
    	position: absolute;
     	top: 100%;
    	left: 50%;
    	border-top-color: var(--color-primary); //var(--color-svg-form);
    	border-width: 4px;
    	margin-left: -4px;
    }

    &.active {
        background: var(--color-primary);
        color: #fff;
        z-index: 99;

        &:before {
        	border-top-color: var(--color-primary);
        }
    }
}

